const ambLight= new THREE.AmbientLight( 0xffffff, 0.8 ); // soft white light
scene.add(ambLight);
const lightMaterial = new THREE.MeshBasicMaterial({color: 0xffbb00})



let mixer = null

gltfLoader.load('toyota_ae86_scene/ae86.gltf', function (gltf){
    console.log(gltf)
    mixer = new THREE.AnimationMixer(gltf.scene)
    for(let i = 1; i < 5; i++){
        const action = mixer.clipAction(gltf.animations[i])
        action.play()
        action.setEffectiveTimeScale(15)
    }
    const action = mixer.clipAction(gltf.animations[0])
    action.play()
    const action2 = mixer.clipAction(gltf.animations[5])
    action2.play()
    action2.setEffectiveTimeScale(0.2)
    for(let i = 10; i < 14; i++){
        gltf.scene.children[0].children[i].material = lightMaterial
        gltf.scene.children[0].children[i].layers.toggle(BLOOM_SCENE)
    }
    
    gltf.scene.children[9].children[4].layers.toggle(BLOOM_SCENE)
    gltf.scene.children[9].children[4].material = lightMaterial
    for(let i = 0; i < 12; i++){
        gltf.scene.children[9].children[4].children[i].material = lightMaterial
        gltf.scene.children[9].children[4].children[i].layers.toggle(BLOOM_SCENE)
    }
 
    scene.add(gltf.scene);
   
})



