let materialArray = [];
let texture_ft = new THREE.TextureLoader().load( 'skybox/back.png');
let texture_bk = new THREE.TextureLoader().load( 'skybox/front.png');
let texture_up = new THREE.TextureLoader().load( 'skybox/top.png');
let texture_dn = new THREE.TextureLoader().load( 'skybox/bottom.png');
let texture_rt = new THREE.TextureLoader().load( 'skybox/right.png');
let texture_lf = new THREE.TextureLoader().load( 'skybox/left.png');
  
materialArray.push(new THREE.MeshBasicMaterial( { map: texture_ft }));
materialArray.push(new THREE.MeshBasicMaterial( { map: texture_bk }));
materialArray.push(new THREE.MeshBasicMaterial( { map: texture_up }));
materialArray.push(new THREE.MeshBasicMaterial( { map: texture_dn }));
materialArray.push(new THREE.MeshBasicMaterial( { map: texture_rt }));
materialArray.push(new THREE.MeshBasicMaterial( { map: texture_lf }));
   
for (let i = 0; i < 6; i++){
    materialArray[i].side = THREE.BackSide;
}

   
let skyboxGeo = new THREE.BoxGeometry( 10000, 10000, 10000 );
let skybox = new THREE.Mesh( skyboxGeo, materialArray );
scene.add( skybox );
// skybox.layers.toggle(BLOOM_SCENE)

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(40, sizes.width / sizes.height, 0.1, 30000)
camera.position.set(20,10,70)
// camera.position.set(10,3,15)
camera.lookAt(-1,20,0)
scene.add(camera)

// // Controls
// const controls = new OrbitControls(camera, canvas)
// controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

// textureLoader.load('bg.png', function(texture){
//     scene.background = texture;
// })


const renderScene = new RenderPass( scene, camera );
const bloomComposer = new EffectComposer( renderer );
bloomComposer.renderToScreen = false;
bloomComposer.addPass(new RenderPass(scene, camera) );
bloomComposer.addPass(new UnrealBloomPass(
    new THREE.Vector2(window.innerWidth,window.innerHeight),
    8,
    0.1,
    0.1
))

const finalPass = new ShaderPass(
    new THREE.ShaderMaterial( {
        uniforms: {
            baseTexture: { value: null },
            bloomTexture: { value: bloomComposer.renderTarget2.texture }
        },
        vertexShader: document.getElementById( 'vertexshader' ).textContent,
        fragmentShader: document.getElementById( 'fragmentshader' ).textContent,
        defines: {}
    } ), 'baseTexture'
);
finalPass.needsSwap = true;



const finalComposer = new EffectComposer( renderer );
finalComposer.addPass( renderScene );
let saoPass = new SAOPass(scene, camera, false, true)
saoPass.params.saoBias = 1
saoPass.params.saoIntensity = 0.05
saoPass.params.saoScale = 1
saoPass.params.saoKernelRadius = 70
saoPass.params.saoMinResolution = 0
saoPass.params.saoBlur = true
saoPass.params.saoBlurRadius = 8
saoPass.params.saoBlurStdDev = 4
saoPass.params.saoBlurDepthCutoff = 0.01
//composer.addPass(saoPass)
finalComposer.addPass(new SMAAPass(
    window.innerWidth * renderer.getPixelRatio(),
    window.innerHeight * renderer.getPixelRatio()))
finalComposer.addPass(new AfterimagePass(0.8))
finalComposer.addPass(new FilmPass(0.3, 0.3, 300, false ))
finalComposer.addPass( finalPass );

/**
 * Animate
 */
const clock = new THREE.Clock()

let previousTime = 0;

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime
    // Update controls
    // controls.update()
    //Update mixer
    if( mixer !== null){
        mixer.update(deltaTime)
    }
    
    renderBloom(true);
    // Render

    
    finalComposer.render();
    
    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()

function renderBloom( mask ) {

    if ( mask === true ) {

        scene.traverse( darkenNonBloomed );
        bloomComposer.render();
        scene.traverse( restoreMaterial );

    } else {

        camera.layers.set( BLOOM_SCENE );
        bloomComposer.render();
        camera.layers.set( ENTIRE_SCENE );

    }

}

function darkenNonBloomed( obj ) {

    if ( obj.isMesh && bloomLayer.test( obj.layers ) === false ) {

        materials[ obj.uuid ] = obj.material;
        obj.material = darkMaterial;

    }

}

function restoreMaterial( obj ) {

    if ( materials[ obj.uuid ] ) {

        obj.material = materials[ obj.uuid ];
        delete materials[ obj.uuid ];

    }

}

